<template>
    <div class="maxBox">
       
         <DateSearch @func="changeDate" @selectVal='selectVal' :search="type"></DateSearch>
        <div class="orderNum">
            <el-row>
            <el-col :span="5"><div class="grid-content ">
                <p>订单数(昨日)</p>
                <span>{{orderAllData.orderCount.first}}</span>
                <span  class="goup" v-if="orderAllData.orderCount.secord>0">较前一日&nbsp;↑&nbsp;{{orderAllData.orderCount.third}}&nbsp;</span>
                <span  class="decline" v-else-if="orderAllData.orderCount.secord<0">较前一日&nbsp;↓&nbsp;{{orderAllData.orderCount.third}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{orderAllData.orderCount.third}}&nbsp;</span>
                </div>
            </el-col>
            <el-col :span="5"><div class="grid-content ">
                <p>订单金额(昨日)</p>
                <span>{{orderAllData.money.first/100}}</span>
                <span  class="goup" v-if="orderAllData.money.secord>0">较前一日&nbsp;↑&nbsp;{{orderAllData.money.third}}&nbsp;</span>
                <span  class="decline" v-else-if="orderAllData.money.secord<0">较前一日&nbsp;↓&nbsp;{{orderAllData.money.third}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{orderAllData.money.third}}&nbsp;</span>
                 </div>
            </el-col>
            <el-col :span="5"><div class="grid-content ">
                <p>下单人数(昨日)</p>
                <span>{{orderAllData.personNum.first}}</span>
                <span  class="goup" v-if="orderAllData.personNum.secord>0">较前一日&nbsp;↑&nbsp;{{orderAllData.personNum.third}}&nbsp;</span>
                <span  class="decline" v-else-if="orderAllData.personNum.secord<0">较前一日&nbsp;↓&nbsp;{{orderAllData.personNum.third}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{orderAllData.personNum.third}}&nbsp;</span></div>
            </el-col>
            <el-col :span="5"><div class="grid-content ">
                <p>支付人数(昨日)</p>
                <span>{{orderAllData.payNum.first}}</span>
                <span  class="goup" v-if="orderAllData.payNum.secord>0">较前一日&nbsp;↑&nbsp;{{orderAllData.payNum.third}}&nbsp;</span>
                <span  class="decline" v-else-if="orderAllData.payNum.secord<0">较前一日&nbsp;↓&nbsp;{{orderAllData.payNum.third}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{orderAllData.payNum.third}}&nbsp;</span></div>
            </el-col>
            <el-col :span="4"><div class="grid-content ">
                <p>转化率(昨日)</p>
                <span>{{orderAllData.rate.first}}</span>
                <span  class="goup" v-if="orderAllData.rate.secord>0">较前一日&nbsp;↑&nbsp;{{orderAllData.rate.third}}&nbsp;</span>
                <span  class="decline" v-else-if="orderAllData.rate.secord<0">较前一日&nbsp;↓&nbsp;{{orderAllData.rate.third}}&nbsp;</span>
                <span  class="just" v-else>较前一日&nbsp;→&nbsp;{{orderAllData.rate.third}}&nbsp;</span></div>
            </el-col>
        </el-row>
        </div>
        
        <div class="box">
            <span class="outchart" @click="dataExport" style="z-index:99999999999">导出</span>
             <div id="orderChart">
        </div>
        </div>
         <div class="box">
            <div class="titleBar">
              <span>订单分布</span>
            </div>
           <el-row>
            <el-col :span="24"><div class="grid-content" id="orderDist"></div></el-col>
          </el-row>
        </div>
         <div class="box">
            <div class="titleBar">
              <span>套餐分布</span>
            </div>
            <div class="list"> 
                <span v-for="(item, index) in serviceList.list" :key="item.id" @click="packDistChange(index,item)" :class="serviceList.index == index ? 'select': ''">       
                {{ item.value }}    
                 </span> 
            </div>
           <el-row>
            <el-col :span="24"><div class="grid-content" id="packageDist"></div></el-col>
          </el-row>
        </div>
    </div>
</template>
<script>
import DateSearch from './components/DateSearch'
import { EleResize} from "../../utils/resize";
import { getOrderCount,getOrderCountChart,getOrderDistChart,getPackageDistChart,getOrderCountExcel} from '../../api/statictics';
export default {
    data() {
        return {
            valueDate: '',
            type:true,
            selectObj :{
                value: ''
            },
            orderAllData:{
                orderCount:{
                    first:'',
                    secord:'',
                    third:''
                },
                money:{
                    first:'',
                    secord:'',
                    third:''
                },
                personNum:{
                    first:'',
                    secord:'',
                    third:''
                },
                payNum:{
                    first:'',
                    secord:'',
                    third:''
                },
                rate:{
                    first:'',
                    secord:'',
                    third:''
                },
            }, //订单总数返回结果
            serviceList:{
             index:0,
             serviceName:'云机购买',
             list:[
                 {
                     id:'0',
                     value:'云机购买'
                 },
                 {
                     id:'1',
                     value:'云机续费'
                 },
{
                     id:'2',
                     value:'IP代理'
                 },
                 {
                     id:'3',
                     value:'设备大师'
                 },
                 {
                     id:'4',
                     value:'窗口多开服务'
                 },
                 {
                     id:'5',
                     value:'多路实时直播'
                 },
               
                 {
                     id:'6',
                     value:'日志调试'
                 },
                 {
                     id:'7',
                     value:'高级调试ADB'
                 },
             ]
           },
            isStart:false,//判断首次是否通过日期监听
        };
    },
     mounted(){
      this.orderCount();
      this.orderChart();
      this.orderDist();
      this.packageDist();
    },
    methods:{
        /**
         * 平台类型判断
         */
      selectVal(val){
       this.selectObj.value= val;
       this.orderCount();
       this.orderChart();
       this.orderDist();
       this.packageDist();
      },
     /**
       * 日期返回值
       */
      changeDate(msg){
          this.valueDate = msg;
          if(this.isStart){
           this.orderChart();
           this.orderDist();
           this.packageDist();
        }else{
          this.isStart = !this.isStart;
        }
         
      },
      /**
       * 订单统计，对比
       */
      orderCount(){
         let params = new Object();
             params.client = this.selectObj.value
            getOrderCount(params)
            .then(res => {
                if (res.resultCode == 0) {
                    this.orderAllData = res.resultInfo
                }
            })
            .catch(error => {
                console.log(error)
            });
      },
        /**
         * 订单统计
         */
        orderChart(){
            let params = new Object();
             params.client = this.selectObj.value;
             params.beginTime = this.valueDate[0];
             params.endTime = this.valueDate[1]
            getOrderCountChart(params)
            .then(res => {
                if (res.resultCode == 0) {
                   let data = res.resultInfo;
                   let myChart = this.$echarts.init(document.getElementById('orderChart'));
                   let option = {
                        tooltip: {
                            trigger: 'axis'
                        },
                        legend: {
                            data:data.data1
                        },
                        grid: {
                            left: '1%',
                            right: '2%',
                            bottom: '8%',
                            containLabel: true
                        },
                       
                        xAxis: {
                            type: 'category',
                            boundaryGap: false,
                            data: data.data2,
                        },
                        yAxis: {
                            type: 'value',
                            axisLine:{    
                            show:false
                            },
                            axisTick:{    
                            show:false
                            },
                            splitLine: { 
                                lineStyle:{
                                type:'dashed'
                            }
                            } 
                        },
                        series: [
                            {
                                name: '支付人数',
                                type: 'line',
                                stack: '总量',
                                data: data.list.payNum
                            },
                            {
                                name: '订单数',
                                type: 'line',
                                stack: '总量',
                                data: data.list.orderCount
                            },
                            {
                                name: '订单金额',
                                type: 'line',
                                stack: '总量',
                                data: data.list.money
                            },
                            {
                                name: '转化率',
                                type: 'line',
                                stack: '总量',
                                data: data.list.rate
                            }
                            
    ]
                   };
                    myChart.setOption(option);
                    let resizeDiv = document.getElementById('orderChart')
                    let listener  =function(){
                    myChart.resize();
                    }
                        EleResize.on(resizeDiv,listener);
                        }
                    })
                    .catch(error => {
                        console.log(error)
                    });
        },
        /**
         * 数据导出
         */
        dataExport(){
           let params = new Object();
               params.client = this.selectObj.value;
               params.beginTime = this.valueDate[0];
               params.endTime = this.valueDate[1]
            getOrderCountExcel(params).then(res => {
                let blob = new Blob([res], {type: 'application/vnd.openxmlformats-  officedocument.spreadsheetml.sheet;charset=utf-8'}); //type这里表示xlsx类型
                let downloadElement = document.createElement('a');
                let href = window.URL.createObjectURL(blob); //创建下载的链接
            　　downloadElement.href = href;
            　　downloadElement.download = '订单总数.xlsx'; //下载后文件名
            　　document.body.appendChild(downloadElement);
            　　downloadElement.click(); //点击下载
            　　document.body.removeChild(downloadElement); //下载完成移除元素
            　　window.URL.revokeObjectURL(href); //释放掉blob对象 
            })
            .catch(error => {
                console.log(error)
            });
        },
        /**
         * 订单分布
         */
         orderDist(){
            let params = new Object();
             params.client = this.selectObj.value;
             params.beginTime = this.valueDate[0];
             params.endTime = this.valueDate[1]
            getOrderDistChart(params)
            .then(res => {
                if (res.resultCode == 0) {
                      let list  = res.resultInfo;
                         list.name.unshift("name")
                       let myChart = this.$echarts.init(document.getElementById('orderDist'));
                        let option = {
                            legend: {
                                icon: 'react',
                                type:'scroll'
                            },
                            tooltip: {
                                trigger: 'axis',
                                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                                }
                            },
                            dataset: {
                                dimensions:  list.name,
                                source: list.data
                            },
                            grid: {
                                        left: '1%',
                                        right: '2%',
                                        bottom: '3%',
                                        containLabel: true
                                    },
                            xAxis: {type: 'category'},
                            yAxis: {
                                        type: 'value',
                                        axisLine:{    
                                        show:false
                                        },
                                        axisTick:{    
                                        show:false
                                        },
                                        splitLine: { 
                                            lineStyle:{
                                            type:'dashed'
                                        }
                                        } 
                                    },
                            series: [
                                {type: 'bar'},
                                {type: 'bar'},
                                {type: 'bar'},
                                {type: 'bar'},
                                {type: 'bar'},
                                {type: 'bar'},
                                {type: 'bar'},
                                {type: 'bar'},
                            ]
                        };
                        myChart.setOption(option);
                        let resizeDiv = document.getElementById('orderDist')
                        let listener  =function(){
                        myChart.resize();
                        }
                            EleResize.on(resizeDiv,listener);
                            }
                        })
                        .catch(error => {
                            console.log(error)
                        });
        },
        /**
         * 套餐类型选择
         */
        packDistChange(index,item){
            this.serviceList.index = index
            this.serviceList.serviceName = item.value;
            this.packageDist();
        },
        /**
         * 套餐分布
         */
        packageDist(){
            let params = new Object();
             params.client = this.selectObj.value;
             params.goodsName = this.serviceList.serviceName;
             params.beginTime = this.valueDate[0];
             params.endTime = this.valueDate[1]
            getPackageDistChart(params)
            .then(res => {
                if (res.resultCode == 0) {
                      let list = res.resultInfo;
                      let myChart = this.$echarts.init(document.getElementById('packageDist'));
                        let option = {
                            tooltip: {
                                trigger: 'axis',
                                axisPointer: {
                                    type: 'shadow'
                                }
                            },
                            grid: {
                                top:'3%',
                                left: '3%',
                                right: '4%',
                                bottom: '15%',
                                containLabel: true
                            },
                            xAxis: {
                                type: 'value',
                                boundaryGap: [0, 0.01]
                            },
                            yAxis: {
                                type: 'category',
                                data:list.name
                            },
                            series: [
                                {
                                    name: '订单数',
                                    type: 'bar',
                                    barWidth: 15, // 柱子宽度
                                    data: list.data,
                                    itemStyle : {  
                                        normal : {  
                                            color:'#409eff',  
                                            lineStyle:{  
                                            color:'#409eff',  
                                            }  
                                        }  
                                    }, 
                                },
                            
                            ]
                        };
                        myChart.setOption(option);
                        let resizeDiv = document.getElementById('packageDist')
                        let listener  =function(){
                        myChart.resize();
                        }
                            EleResize.on(resizeDiv,listener); 
                            }
            })
            .catch(error => {
                console.log(error)
            });
          
        }
    
    },
    components:{
          DateSearch
        }
}
</script>
<style lang="less" scoped>
 @import "index.less";
 .null-box{
 margin-top: 15px;
}
 .list{
     padding: 15px 0px 0px 15px;
     span{
         padding-right: 10px;
         cursor: pointer;
     }
     .select{
        color: #409EFF;
     }
 }
 .list{
     padding: 15px 0px 0px 15px;
     display: flex;
     span{
         padding-right: 10px;
         cursor: pointer;
         border: 1px solid #ccc;
        // margin: -5px;
        padding: 5px 15px;
        font-size: 14px;
        border-right: 0px;
     }
     span:last-child{
           border-right: 1px solid #ccc;
     }
     .select{
        color: #409EFF;

     }
 }
 .orderNum{
    width: 100%;
    height: 180px;
    border: 1px solid #ccc;
    margin-bottom: 15px;
    .grid-content{
    // border: 1px solid #ccc;
    p{
        text-align: center;
        font-size: 20px;
    }
    span{
        display: block;
        text-align: center;
        font-size: 40px;
    }
    .decline{
        font-size: 14px;
        color: rgba(67, 207, 124, 1);
    }
    .goup{
         font-size: 14px;
         color:red;
    }
    .just{
         font-size: 14px;
         color:#ccc;
    }
    }
 }
 #orderChart{
    width: 100%;
    height: 100%;
    margin-bottom: 15px;
 }
 .outchart{
    position: relative;
    left: 97%;
    top: 5%;
    font-size: 16px;
    color: rgba(42, 130, 228, 1);
    cursor: pointer;
 }
 .left{
   margin-left: 20px;
 }
</style>



